.box {
  height: 100%;
  background: #f8f8fd;
  text-align: left;
  overflow-x: scroll;
}

.weights {
  stroke: #ff4000;
  stroke-width: 0.05;
  fill: transparent;
}

.normal {
  stroke: #808080;
  stroke-width: 0.025;
  fill: transparent;
}

.minor {
  stroke: #c000c0;
  stroke-width: 0.03;
  fill: transparent;
}

.major {
  stroke: #0020ff;
  stroke-width: 0.04;
  fill: transparent;
}

.year {
  stroke: #ff0040;
  stroke-width: 0.04;
  fill: transparent;
}
